*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
    outline:none;
}

a {
    text-decoration: none;
}

ul,
ol,
li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

html,
body {
    height: 100%;
    // line-height: 1;
    font-size: 14px;
    color: #252B42;
    font-weight: 500;
    background-color: #FAFAFA;

}

.App {
    min-height: 100%;
    max-width: 1200px;
    display: flex;
    margin: 0px auto;
    flex-direction: column;
    background-color: #FAFAFA;
    flex-wrap: wrap;
    padding: 0 5px;

    .displayNone{
        display: none;
    }
}

.content {
    background-color: #E5E5E5;
    flex: 1 1 auto;
    // padding-top: 55px;
    // padding-bottom: 55px;


    &::-webkit-scrollbar {
        width: 0;
      }
}

